*{font-family:Arial,Helvetica,sans-serif}.form{margin-top:5vh;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:repeat(7, 1fr);grid-gap:20px 30px;gap:20px 30px;width:50vw}@media screen and (max-width: 480px){.form{display:flex;flex-direction:column;justify-content:center;align-items:center;width:85vw;font-size:1.15rem}}@media screen and (min-width: 480px)and (max-width: 768px){.form{display:flex;flex-direction:column;justify-content:center;width:85vw;font-size:1.15rem}}.form__item--password{position:relative}.form__show-button{position:absolute;color:gray;top:55%;right:3%;background:transparent;border:none;text-decoration:underline;cursor:pointer}.form__item{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start}@media screen and (max-width: 480px){.form__item{width:100%}}.form__item--info{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;grid-area:3/1/5/2}.form__item--confirm{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-area:5/1/7/3}@media screen and (max-width: 480px){.form__item--confirm{position:relative;display:flex;flex-direction:column;justify-content:flex-end;align-items:center}}.form__item--confirm:hover{cursor:pointer}.form__box{font-family:"Cambay",sans-serif;padding:10px;width:100%;height:40px;border:1px solid #d3d3d3;font-size:1rem}@media screen and (max-width: 480px){.form__box{width:85vw}}@media screen and (min-width: 480px)and (max-width: 768px){.form__box{width:85vw}}.form__box--phone{font-family:"Cambay",sans-serif;font-size:1rem;width:60%;height:40px;border:1px solid #d3d3d3;padding:10px}@media screen and (min-width: 480px)and (max-width: 768px){.form__box--phone{width:50vw}}.form__box--info{font-family:"Cambay",sans-serif;font-size:1rem;width:100%;height:110px;padding-left:10px;padding-top:10px;padding-bottom:70px;border:1px solid #d3d3d3;resize:none}@media screen and (min-width: 480px)and (max-width: 768px){.form__box--info{width:85vw}}.form__label{margin-bottom:.6em;font-size:.8rem;font-family:"Cambay",sans-serif}.form__button{position:absolute;display:block}@media screen and (max-width: 480px){.form__button{position:inherit;width:85vw}}@media screen and (min-width: 480px)and (max-width: 768px){.form__button{position:inherit;width:50vw}}.form__button-text{position:absolute;color:#fff;font-weight:600;top:50%;left:50%;transform:translate(-50%, -50%)}@media screen and (max-width: 480px){.form__button-text{top:50%;left:50%}}
.spinner-icon{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh}.spinner-icon__image{animation:rotation 1.5s infinite linear;width:5vw;height:auto}@media screen and (max-width: 480px){.spinner-icon__image{width:10vw;margin-bottom:20vh}}@media screen and (min-width: 480px)and (max-width: 768px){.spinner-icon__image{width:20vw;margin-bottom:20vh}}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
.complete{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:25vh;width:30vw}@media screen and (max-width: 480px){.complete{width:80vw;height:30vh}}@media screen and (min-width: 480px)and (max-width: 768px){.complete{width:50vw;height:30vh}}.complete__content{text-align:center;font-family:"Cambay",sans-serif;font-size:1.2rem}.complete__image{width:6vw;height:auto}@media screen and (max-width: 480px){.complete__image{width:15vw;height:auto}}@media screen and (min-width: 480px)and (max-width: 768px){.complete__image{width:10vw;height:auto}}
*{margin:0;padding:0;box-sizing:border-box}.sign-in{position:relative;height:100vh;width:100vw;overflow:hidden;background-color:#f8f5f0;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:0px 0px;gap:0px 0px}@media screen and (min-width: 480px)and (max-width: 768px){.sign-in{display:flex;flex-direction:column;justify-content:flex-start;align-items:center}}.sign-in__header{font-family:"Noto Sans",sans-serif;font-weight:1200;margin-top:5vh;display:flex;justify-content:center;align-self:flex-start;grid-area:1/2/2/3}.sign-in__form{margin-top:7vh;grid-area:1/2/2/3}.sign-in__form--inactive{display:none}.sign-in__spinner--active{display:flex;justify-content:center;align-items:center;grid-area:1/2/2/3}.sign-in__spinner--inactive{display:none}.sign-in__complete--active{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;margin-top:15vh;grid-area:1/2/2/3}@media screen and (max-width: 480px){.sign-in__complete--active{margin-top:5vh;justify-content:flex-start}}.sign-in__complete--inactive{display:none}.sign-in__left-illustration{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.sign-in__left-illustration--inactive{display:none}.sign-in__right-illustration{display:flex;flex-direction:column;justify-content:center;align-items:center;grid-area:1/3/2/4}.sign-in__right-illustration--inactive{display:none}.sign-in__image--right{position:absolute;width:40vw;top:9%;right:-17%}@media screen and (max-width: 480px){.sign-in__image--right{position:absolute;width:35vw;top:2%;right:-10%}}@media screen and (min-width: 480px)and (max-width: 768px){.sign-in__image--right{position:absolute;width:30vw;top:0%;right:-10%}}.sign-in__image--left{position:absolute;top:25%;left:-1%;width:20vw}@media screen and (max-width: 480px){.sign-in__image--left{position:absolute;top:5%;left:-2%}}@media screen and (min-width: 480px)and (max-width: 768px){.sign-in__image--left{position:absolute;top:3%;left:-2%}}
